*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    height: 1fr;
    background: linear-gradient(334deg, #6b97f7, #7525e2, #f7137e);
    background-size: 180% 180%;
    animation: gradient-animation 6s ease infinite;
    display: flex;
    flex-direction: column;
}

@keyframes gradient-animation {
    0% {
        background-position: 0 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0 50%;
    }
}

header, #content {
    background-image: linear-gradient(-10deg, #475569 0%, #2563eb 25%, #1e3a8a 50%, #4a044e 100%);
    background-size: 200% 200%;
    border-radius: 10px;
}
/* Header styling */
header {
    display: flex;
    height: 100px;
    align-items: center;
    margin: 20px;
}

nav {
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: right;
    padding: 20px;
}

.button-link, .cta {
    margin: 40px;
    background: none;
    border: none;
}

/* Button styling */
.button-link, .cta > span {
    cursor: pointer; /* Change cursor to pointer on hover */
    background: linear-gradient(to right, #ef4444, #fed1c7); /* Gradient colors */
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: transparent;
    transition: transform 0.2s ease-in-out; /* Smooth scaling  */
    font-weight: bold;
    font-size: 14px;
    padding-bottom: 7px;
    letter-spacing: 4px;
    padding-right: 15px;
    text-transform: uppercase;
}

#arrow-horizontal {
    fill: url(#SvgjsLinearGradient1011);
}

.cta svg {
    transform: translateX(-8px);
    transition: all 0.3s ease;
}

.cta:hover svg {
    transform: translateX(0);
}

.cta:active svg {
    transform: scale(0.9);
}

.hover-underline-animation {
    position: relative;
    padding-bottom: 20px;
}

.hover-underline-animation:after {
    content: "";
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background: linear-gradient(to right, #ef4444, #fed1c7);;
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
}

.cta:hover .hover-underline-animation:after {
    transform: scaleX(1);
    transform-origin: bottom left;
}

.button-link:hover {
    opacity: 0.8;
}

.svg {
    flex: 1 1 auto;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: left;
}

/* SVG styling */
.svg > svg {
    transform: scale(0.4);
    fill: white;
}

#content {
    height: 100%;
    margin: 20px 400px;
    padding: 60px 100px;
}

img {
    border-radius: 10px;
    border: 15px solid black;
    height: auto;
    align-self: center;
}

div > * {
    margin-top: 10px;
    margin-bottom: 10px;
    text-align: center;
}

div > p {
    color: white;
    font-size: 20px;
    border-radius: 10px;
    padding: 10px;
    background: linear-gradient(334deg, #6b97f7, #7525e2, #f7137e);
    background-size: 180% 180%;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA,yBAAyB;AACzB;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,WAAW;IACX,8DAA8D;IAC9D,0BAA0B;IAC1B,8CAA8C;IAC9C,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI;QACI,0BAA0B;IAC9B;IACA;QACI,6BAA6B;IACjC;IACA;QACI,0BAA0B;IAC9B;AACJ;;AAEA;IACI,6FAA6F;IAC7F,0BAA0B;IAC1B,mBAAmB;AACvB;AACA,mBAAmB;AACnB;IACI,aAAa;IACb,aAAa;IACb,mBAAmB;IACnB,YAAY;AAChB;;AAEA;IACI,cAAc;IACd,aAAa;IACb,mBAAmB;IACnB,sBAAsB;IACtB,aAAa;AACjB;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,YAAY;AAChB;;AAEA,mBAAmB;AACnB;IACI,eAAe,EAAE,sCAAsC;IACvD,uDAAuD,EAAE,oBAAoB;IAC7E,6BAA6B;IAC7B,oCAAoC;IACpC,qBAAqB;IACrB,kBAAkB;IAClB,sCAAsC,EAAE,oBAAoB;IAC5D,iBAAiB;IACjB,eAAe;IACf,mBAAmB;IACnB,mBAAmB;IACnB,mBAAmB;IACnB,yBAAyB;AAC7B;;AAEA;IACI,mCAAmC;AACvC;;AAEA;IACI,2BAA2B;IAC3B,yBAAyB;AAC7B;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,kBAAkB;IAClB,oBAAoB;AACxB;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,WAAW;IACX,oBAAoB;IACpB,WAAW;IACX,SAAS;IACT,OAAO;IACP,uDAAuD;IACvD,8BAA8B;IAC9B,oCAAoC;AACxC;;AAEA;IACI,oBAAoB;IACpB,6BAA6B;AACjC;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,cAAc;IACd,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,qBAAqB;AACzB;;AAEA,gBAAgB;AAChB;IACI,qBAAqB;IACrB,WAAW;AACf;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;IACnB,wBAAwB;IACxB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;IAChB,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,mBAAmB;IACnB,aAAa;IACb,8DAA8D;IAC9D,0BAA0B;AAC9B","sourcesContent":["/* Reset and box-sizing */\n*{\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\nbody {\n    height: 1fr;\n    background: linear-gradient(334deg, #6b97f7, #7525e2, #f7137e);\n    background-size: 180% 180%;\n    animation: gradient-animation 6s ease infinite;\n    display: flex;\n    flex-direction: column;\n}\n\n@keyframes gradient-animation {\n    0% {\n        background-position: 0 50%;\n    }\n    50% {\n        background-position: 100% 50%;\n    }\n    100% {\n        background-position: 0 50%;\n    }\n}\n\nhe